import { ContentContainer } from "components/Content";
import { TextInput } from "components/Input";
import {
  LeftTitle,
  CheckButton,
  CheckContainer,
  SignupButton,
  ToggleButton,
  Select,
} from "./styled";
import { useEffect, useState } from "react";
import { DaumMapModal } from "components/Map";
import { getBloodTypeCategories } from "api/CategoriesAPI";
import {
  postDuplicateLoginId,
  postDuplicatePhoneNumber,
  postDuplicateNickName,
  postMember,
} from "api/MembersAPI";
import { ROUTES_PATH_LOGIN } from "constants/Routes";
import { useNavigate } from "react-router-dom";

function Signup() {
  const navigate = useNavigate();
  const [id, setId] = useState("");
  const [isIdChecked, setIsIdChecked] = useState(false);
  const [pw, setPw] = useState("");
  const [pwCheck, setPwCheck] = useState("");
  const [name, setName] = useState("");
  const [birth, setBirth] = useState("");
  const [isMan, setIsMan] = useState(true);
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [isPhoneChecked, setIsPhoneChecked] = useState(false);
  const [address, setAddress] = useState("");
  const [detailAddress, setDetailAddress] = useState("");
  const [blood, setBlood] = useState(1);
  const [nickname, setNickname] = useState("");
  const [isNicknameChecked, setIsNicknameChecked] = useState(false);
  const [modalVisible, setModalVisible] = useState(false);
  const [bloodTypes, setBloodTypes] = useState([]);
  useEffect(() => {
    getBloodTypeCategories().then((res) => setBloodTypes(res.data));
  }, []);
  return (
    <>
      {modalVisible && (
        <DaumMapModal
          setModalVisible={setModalVisible}
          setAddress={setAddress}
        />
      )}
      <ContentContainer style={{ alignItems: "start" }}>
        <LeftTitle>ID</LeftTitle>
        <CheckContainer>
          <TextInput
            style={{ flex: 1 }}
            placeholder="ID "
            value={id}
            onChange={(e) => {
              setId(e.target.value);
              setIsIdChecked(false);
            }}
          ></TextInput>
          <CheckButton
            checked={isIdChecked}
            onClick={() => {
              postDuplicateLoginId(id).then(() => {
                setIsIdChecked(true);
              });
            }}
          >
            
            <br />
            
          </CheckButton>
        </CheckContainer>
        <LeftTitle>PW</LeftTitle>
        <TextInput
          placeholder="PW "
          value={pw}
          onChange={(e) => setPw(e.target.value)}
          type="password"
        ></TextInput>
        <LeftTitle>PW </LeftTitle>
        <TextInput
          placeholder="PW "
          value={pwCheck}
          onChange={(e) => setPwCheck(e.target.value)}
          type="password"
        ></TextInput>
        <LeftTitle></LeftTitle>
        <TextInput
          placeholder=" "
          value={name}
          onChange={(e) => setName(e.target.value)}
        ></TextInput>
        <LeftTitle></LeftTitle>
        <TextInput
          placeholder=" "
          value={birth}
          onChange={(e) => setBirth(e.target.value)}
        ></TextInput>
        <LeftTitle></LeftTitle>
        <CheckContainer style={{ gap: "3px" }}>
          <ToggleButton checked={isMan} onClick={() => setIsMan(true)}>
            
          </ToggleButton>
          <ToggleButton checked={!isMan} onClick={() => setIsMan(false)}>
            
          </ToggleButton>
        </CheckContainer>
        <LeftTitle></LeftTitle>
        <TextInput
          placeholder=" "
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        ></TextInput>
        <LeftTitle> </LeftTitle>
        <CheckContainer>
          <TextInput
            style={{ flex: 1 }}
            placeholder="-  "
            value={phone}
            onChange={(e) => {
              setPhone(e.target.value);
              setIsPhoneChecked(false);
            }}
          ></TextInput>
          <CheckButton
            checked={isPhoneChecked}
            onClick={() => {
              postDuplicatePhoneNumber(phone).then((res) => {
                setIsPhoneChecked(true);
              });
            }}
          >
            
            <br />
            
          </CheckButton>
        </CheckContainer>
        <LeftTitle></LeftTitle>
        <CheckContainer>
          <TextInput
            style={{ flex: 1 }}
            placeholder=" "
            value={address}
            onChange={(e) => setAddress(e.target.value)}
          ></TextInput>
          <CheckButton onClick={() => setModalVisible(true)}>
            
            <br />
            
          </CheckButton>
        </CheckContainer>
        <TextInput
          style={{ marginTop: "10px" }}
          placeholder="  "
          value={detailAddress}
          onChange={(e) => setDetailAddress(e.target.value)}
        ></TextInput>
        <LeftTitle></LeftTitle>
        <Select value={blood} onChange={(e) => setBlood(e.target.value)}>
          {bloodTypes?.map((bloodType) => (
            <option key={bloodType.id} value={bloodType.id}>
              {bloodType.bloodType}
            </option>
          ))}
        </Select>
        <LeftTitle></LeftTitle>
        <CheckContainer>
          <TextInput
            style={{ flex: 1 }}
            placeholder=" "
            value={nickname}
            onChange={(e) => {
              setNickname(e.target.value);
              setIsNicknameChecked(false);
            }}
          ></TextInput>
          <CheckButton
            checked={isNicknameChecked}
            onClick={() => {
              postDuplicateNickName(nickname).then(() => {
                setIsNicknameChecked(true);
              });
            }}
          >
            
            <br />
            
          </CheckButton>
        </CheckContainer>
        <SignupButton
          onClick={() => {
            postMember({
              loginId: id,
              password: pw,
              rePassword: pwCheck,
              name: name,
              age: +birth,
              gender: isMan,
              email: email,
              phoneNumber: phone,
              address: address + detailAddress,
              bloodType: +blood,
              nickname: nickname,
            }).then((res) => {
              alert(" .");
              navigate(ROUTES_PATH_LOGIN);
            });
          }}
        >
          
        </SignupButton>
      </ContentContainer>
    </>
  );
}
export default Signup;
