import { SubTitle, Input, LoginButton, Logo } from "./styled";
import { TextLink } from "components/Input";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { ROUTES_PATH_HOME } from "constants/Routes";
import { ContentContainer } from "components/Content";

function Login() {
  const navigate = useNavigate();
  const [id, setId] = useState("");
  const [pw, setPw] = useState("");

  const login = () => {
    console.log(id, pw);
    navigate(ROUTES_PATH_HOME);
  };

  return (
    <ContentContainer style={{ paddingTop: "98px" }}>
      <Logo src="png/logo.png"></Logo>
      <SubTitle></SubTitle>
      <Input
        placeholder=""
        value={id}
        onChange={(e) => setId(e.target.value)}
      ></Input>
      <Input
        placeholder=""
        value={pw}
        onChange={(e) => setPw(e.target.value)}
        type="password"
      ></Input>
      <LoginButton onClick={login}></LoginButton>
      <TextLink to="/signup"></TextLink>
      {/* <TextLink href="/idpw">/ </TextLink> */}
    </ContentContainer>
  );
}

export default Login;
